html {
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  font-family: "Asap", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #697084;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}

b,
strong {
  font-weight: 800;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Asap", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

pre,
code {
  font-family: monospace;
  font-weight: 400;
  background: #f5f5f5;
  color: #B4003D;
  display: inline-block;
  padding: 0 5px;
  border-radius: 3px;
}

pre {
  overflow: auto;
}

a {
  color: inherit;
  text-decoration: underline;
}

button {
  font: inherit;
  padding: 0;
  border: 0;
  background: transparent;
  cursor: pointer;
  border-radius: 0;
  color: inherit;
  outline: 0;
}

/* Objects */
.p-button {
  font: inherit;
  padding: 0;
  border: 0;
  background: transparent;
  cursor: pointer;
  border-radius: 0;
  color: inherit;
  outline: 0;
  display: inline-block;
  text-decoration: none;
  border: 0;
  padding: 10px 30px;
  vertical-align: middle;
  background: #B4003D;
  color: #fff;
  border-radius: 9999px;
  font-size: 16px;
  font-weight: 500;
  transition: all .2s ease-out;
}

.p-button:hover {
  background: #9b0034;
}

.p-button--secondary {
  background: #293351;
}

.p-button--secondary:hover {
  background: #202840;
}

.p-button--inverse {
  background: #fff;
  color: #293351;
}

.p-button--inverse:hover {
  color: #293351;
  background: #f0c230;
  box-shadow: 0 2px 10px rgba(32, 32, 32, 0.4);
}

.p-button--large {
  font-size: 20px;
}

.p-button--small {
  font-size: 14px;
  padding: 4px 18px;
}

.p-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  vertical-align: middle;
}

.p-icon svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: currentColor;
}

.p-icon--0-5x {
  width: 12px;
  height: 12px;
}

.p-icon--2x {
  width: 48px;
  height: 48px;
}

.p-icon--button {
  margin-top: -3px;
}

