.p-list-bare {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.p-list-inline {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.p-list-inline li {
  display: inline-block;
}

.p-table {
  overflow: auto;
  margin: 40px 0;
  padding: 5px 20px;
  border-radius: 5px;
  box-shadow: 0 2px 10px rgba(32, 32, 32, 0.2);
}

.p-table table {
  width: 100%;
  border-collapse: collapse;
}

.p-table tr:first-child td {
  padding-top: 30px;
}

.p-table td,
.p-table th {
  padding: 20px 3px;
  white-space: nowrap;
}

.p-table td + td,
.p-table th + th {
  padding-left: 20px;
}

.p-table td {
  font-size: 24px;
}

.p-table th {
  border-bottom: 1px solid #e3e4e8;
  font-weight: 500;
  font-size: 18px;
  text-align: left;
}

.p-tag, .c-tag-list-link {
  background-color: #B4003D;
  color: #fff;
  display: inline-block;
  padding: 1px 6px;
  margin-left: 3px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 800;
  vertical-align: text-bottom;
}

.p-title {
  margin: 0;
}

.p-video,
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}

.p-video iframe,
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Components */
.c-article {
  display: flex;
  flex-direction: column;
}

.c-article + .c-article {
  margin-top: 80px;
}

@media only screen and (min-width: 500px) {
  .c-article {
    flex-direction: row;
    align-items: center;
  }
}

.c-article__header {
  margin-bottom: 20px;
}

.c-article__title {
  margin: 0;
}

.c-article__title a {
  text-decoration: none;
}

.c-article__meta {
  margin: 0;
  font-size: 14px;
}

.c-article .c-article__image {
  flex-shrink: 0;
  margin-bottom: 20px;
}

.c-article .c-article__image img {
  width: 110px;
  margin: 0;
}

@supports (object-fit: cover) {
  .c-article .c-article__image img {
    height: 90px;
    object-fit: cover;
  }
}

@media only screen and (min-width: 500px) {
  .c-article .c-article__image {
    order: 1;
    margin-bottom: 0;
  }
  .c-article .c-article__image img {
    width: 165px;
    margin-left: 20px;
  }
  @supports (object-fit: cover) {
    .c-article .c-article__image img {
      height: 135px;
    }
  }
}

@media only screen and (min-width: 800px) {
  .c-article .c-article__image img {
    width: 220px;
    margin-left: 40px;
  }
  @supports (object-fit: cover) {
    .c-article .c-article__image img {
      height: 180px;
    }
  }
}

.c-content {
  max-width: 740px;
  margin: 60px auto;
}

.c-content h1 {
  text-align: center;
  font-weight: 400;
  margin: 2em 0;
  color: #293351;
}

.c-content h2 {
  color: #293351;
  font-weight: 400;
}

.c-content h3 {
  color: #293351;
  font-weight: 400;
}

.c-content ul,
.c-content ol {
  padding-left: 28px;
}

.c-content li + li {
  margin-top: 5px;
}

.c-content img {
  margin: 50px auto;
  display: block;
}

.c-content img.has-shadow {
  border-radius: 6px;
  box-shadow: 0 2px 10px rgba(32, 32, 32, 0.2);
}

.c-content .video-container {
  margin: 50px auto;
  display: block;
}

.c-content .video-container iframe {
  border-radius: 6px;
  box-shadow: 0 2px 10px rgba(32, 32, 32, 0.2);
}

.c-content .c-content__title {
  margin: 0;
}

.c-content .c-content__meta {
  text-align: center;
}

.c-content__poster {
  max-width: 720px;
  margin: 0 auto;
  display: block;
}

.c-feature {
  padding-top: 70px;
}

.c-feature__title {
  font-size: 28px;
  font-weight: 400;
  color: #293351;
}

.c-feature__description {
  font-size: 18px;
}

.c-feature__description ul, .c-feature__description ol {
  padding-left: 30px;
  list-style: none;
}

.c-feature__description li {
  position: relative;
}

.c-feature__description li:before {
  content: '';
  display: block;
  height: 6px;
  width: 6px;
  border-radius: 6px;
  background: #e3e4e8;
  position: absolute;
  left: -20px;
  top: 14px;
}

.c-feature__image {
  text-align: center;
}

.c-feature__image img {
  max-width: 400px;
}

.c-hero {
  padding: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% top;
  color: #fff;
  position: relative;
  z-index: 0;
}

@media only screen and (max-width: 766px) {
  .c-hero {
    text-align: center;
  }
  .c-hero.is-open .c-hero__nav {
    transform: translate(-50%, 0);
    opacity: 1;
    visibility: visible;
    transition: all .2s ease-out, visibility 0s;
  }
  .c-hero.is-open .c-hero__more-icon {
    opacity: 0;
  }
  .c-hero.is-open .c-hero__cross-icon {
    opacity: 1;
  }
}

.c-hero__gradient {
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(135deg, #293351, #B4003D);
  opacity: 0.9;
  z-index: -1;
}

.c-hero__header {
  display: flex;
  align-items: center;
  position: relative;
}

@media only screen and (max-width: 766px) {
  .c-hero__header {
    display: inline-flex;
    padding: 10px 0;
  }
}

.c-hero__title {
  margin: 0 auto 0 0;
  font-weight: 400;
  font-size: 21px;
}

.c-hero__nav {
  padding-top: 10px;
}

@media only screen and (max-width: 766px) {
  .c-hero__nav {
    position: absolute;
    top: 100%;
    left: 50%;
    background: #fff;
    color: #697084;
    padding: 10px 20px;
    margin-top: -4px;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(32, 32, 32, 0.3);
    transform: translate(-50%, -10px);
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease-out, visibility 0s .2s;
    z-index: 20;
  }
  .c-hero__nav:before {
    content: '';
    display: block;
    height: 10px;
    width: 10px;
    background: inherit;
    border-top-left-radius: 3px;
    transform: translate(-50%) rotate(45deg);
    position: absolute;
    top: -5px;
    left: 50%;
  }
}

.c-hero__link {
  display: inline-block;
  font-size: 15px;
  line-height: 38px;
  text-decoration: none;
}

.c-hero__link:hover {
  opacity: 0.8;
}

@media only screen and (max-width: 766px) {
  .c-hero__link {
    display: block;
    white-space: nowrap;
    line-height: 1.2;
  }
  .c-hero__link + .c-hero__link {
    margin-top: 5px;
  }
  .c-hero__link:hover, .c-hero__link.is-current {
    color: #B4003D;
  }
}

@media only screen and (min-width: 767px) {
  .c-hero__link {
    padding-top: 2px;
    border-bottom: 2px solid transparent;
  }
  .c-hero__link + .c-hero__link {
    margin-left: 30px;
  }
  .c-hero__link.is-current {
    border-color: #fff;
  }
}

.c-hero__mobile-button {
  position: relative;
  background: #fff;
  border-radius: 999px;
  height: 20px;
  width: 20px;
  padding: 4px;
  color: #293351;
  margin-left: 10px;
  backface-visibility: hidden;
}

.c-hero__mobile-button:hover, .c-hero__mobile-button:focus {
  outline: 0;
  opacity: 0.8;
}

@media only screen and (min-width: 767px) {
  .c-hero__mobile-button {
    display: none;
  }
}

.c-hero__more-icon {
  vertical-align: top;
  opacity: 1;
  transition: all .2s ease-out;
}

.c-hero__cross-icon {
  position: absolute;
  top: 4px;
  bottom: 4px;
  left: 4px;
  right: 4px;
  opacity: 0;
  transition: all .2s ease-out;
}

.c-hero__tagline {
  max-width: 620px;
  margin: 0 auto;
  padding: 100px 0;
  font-size: 24px;
  text-align: center;
}

.c-hero__tagline + .c-hero__actions {
  margin-top: -40px;
}

.c-hero__actions {
  margin: 0;
  padding-left: 0;
  list-style: none;
  text-align: center;
  margin-top: 80px;
  margin-left: -20px;
  padding-bottom: 80px;
}

.c-hero__action {
  display: inline-block;
  width: 100%;
  max-width: 250px;
  margin-top: 10px;
  margin-left: 20px;
  vertical-align: top;
}

.c-hero__details {
  margin-top: 6px;
}

.c-hero__video {
  max-width: 740px;
  margin: 0 auto;
  height: 120px;
  margin-bottom: calc(56.25% - 120px);
}

@media only screen and (min-width: 776px) {
  .c-hero__video {
    margin-bottom: 296px;
  }
}

.c-hero__video iframe {
  border-radius: 6px;
  box-shadow: 0 2px 10px rgba(32, 32, 32, 0.38);
  background: #B4003D;
}

.c-newsletter {
  display: flex;
  align-items: center;
}

.c-newsletter__field {
  border: 0;
  padding: 5px;
  outline: 0;
  border-radius: 5px;
  min-width: 250px;
  flex-grow: 1;
}

.c-pagination {
  text-align: center;
  margin: 40px 0;
}

.c-pagination a {
  text-decoration: none;
}

.c-pagination a:hover {
  color: #293351;
}

.c-pagination .page-number {
  display: inline-block;
  line-height: 23px;
  margin: 0 3px;
}

.c-pagination .page-number.current {
  background: #293351;
  color: #fff;
  width: 23px;
  border-radius: 24px;
}

.c-pagination .extend {
  display: inline-block;
  line-height: 23px;
  margin: 0 10px;
}

.c-pricing {
  text-align: center;
}

.c-pricing__range {
  margin: 0 0 0 auto;
  padding: 0;
  width: 100%;
  max-width: 500px;
  height: 24px;
  background: transparent;
  appearance: none;
  outline: 0;
  cursor: pointer;
}

.c-pricing__range::-webkit-slider-runnable-track {
  height: 10px;
